import React, { useState, useEffect } from 'react';
import axios from 'axios';
import {
  Table,
  TableContainer,
  TableHead,
  TableRow,
  TableCell,
  TableBody,
  TextField,
  Paper,
  IconButton,
  TablePagination,
  Grid,
  CircularProgress,
  MenuItem,
  Select,
} from '@mui/material';
import { useNavigate } from 'react-router-dom';
import { Button } from 'react-bootstrap';
import SearchIcon from '@mui/icons-material/Search';
import { toast } from 'react-toastify';
import { useAuth } from './AuthContext';

const AdminTicketList = () => {
  const navigate = useNavigate();
  const [tickets, setTickets] = useState([]);
  const [searchTerm, setSearchTerm] = useState('');
  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(10);
  const [loading, setLoading] = useState(true);
  const [techSupportAgents, setTechSupportAgents] = useState([]);
  const { user } = useAuth();

  useEffect(() => {
    const fetchTicketsAndAgents = async () => {
      try {
        const apiBaseUrl = process.env.REACT_APP_API_KEY;
        const [ticketsResponse, agentsResponse] = await Promise.all([
          axios.get(`${apiBaseUrl}/auth/admin-tickets`),
          axios.get(`${apiBaseUrl}/auth/tech-support-agents`)
        ]);
        setTickets(ticketsResponse.data.tickets);
        setTechSupportAgents(agentsResponse.data.agents);
      } catch (error) {
        console.error('Error fetching data:', error);
      } finally {
        setLoading(false);
      }
    };

    if (user) {
      fetchTicketsAndAgents();
    }
  }, [user]);

  const searchTermLower = searchTerm.toLowerCase();

  const filteredTickets = tickets.filter((ticket) => 
    ticket.subject.toLowerCase().includes(searchTermLower) ||
    ticket.status.toLowerCase().includes(searchTermLower)
  );

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  const handleSubmit = () => {
    navigate('/ticket-form');
  };

  const handleRowClick = (ticketId) => {
    navigate(`/ticketDetail/${ticketId}`);
  };

  const handleStatusChange = async (ticketId, newStatus) => {
    try {
      const apiBaseUrl = process.env.REACT_APP_API_KEY;
      await axios.put(`${apiBaseUrl}/auth/update-ticket-status/${ticketId}`, { status: newStatus });
      setTickets(tickets.map(ticket => ticket._id === ticketId ? { ...ticket, status: newStatus } : ticket));
      toast.success('Status Updated successfully'); // Display success message
    } catch (error) {
      console.error('Error updating ticket status:', error);
    }
  };

  const handleAgentChange = async (ticketId, newAgentId) => {
    try {
      const apiBaseUrl = process.env.REACT_APP_API_KEY;
      await axios.put(`${apiBaseUrl}/auth/update-ticket-agent/${ticketId}`, { agentId: newAgentId });
      setTickets(tickets.map(ticket => ticket._id === ticketId ? { ...ticket, agentId: newAgentId } : ticket));
      toast.success('Agent Assigned successfully'); // Display success message
    } catch (error) {
      console.error('Error updating ticket agent:', error);
    }
  };

  return (
    <div className="container">
      <h5 style={{ textAlign: 'left', color: '#8F00FF' }}>Admin Tickets</h5>
     

      <TextField
        label="Search by Subject or Status"
        variant="outlined"
        fullWidth
        value={searchTerm}
        onChange={(e) => setSearchTerm(e.target.value)}
        InputProps={{
          endAdornment: (
            <IconButton>
              <SearchIcon />
            </IconButton>
          ),
        }}
        style={{ marginBottom: 16 }}
      />

      {loading ? (
        <CircularProgress />
      ) : (
        <>
          <TableContainer component={Paper}>
            <Table>
              <TableHead style={{backgroundColor:'#8F00FF'}}>
                <TableRow>
                  <TableCell style={{ color:'white' }}><strong>Ticket ID</strong></TableCell>
                  <TableCell style={{ color:'white' }}><strong>Subject</strong></TableCell>
                  <TableCell style={{ color:'white' }}><strong>Description</strong></TableCell>
                  <TableCell style={{ color:'white' }}><strong>Status</strong></TableCell>
                  <TableCell style={{ color:'white' }}><strong>Created Date</strong></TableCell>
                  <TableCell style={{ color:'white' }}><strong>Update Status</strong></TableCell>
                  <TableCell style={{ color:'white' }}><strong>Assigned Tech Support</strong></TableCell> 
                </TableRow>
              </TableHead>
              <TableBody>
                {filteredTickets
                  .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                  .map((ticket) => (
                    <TableRow key={ticket._id} style={{ cursor: 'pointer' }}>
                      <TableCell>{ticket._id}</TableCell>
                      <TableCell>{ticket.subject}</TableCell>
                      <TableCell>{ticket.description}</TableCell>
                      <TableCell>{ticket.status}</TableCell>
                      <TableCell>{new Date(ticket.createdAt).toLocaleDateString()}</TableCell>
                      <TableCell>
                        <Select
                          value={ticket.status}
                          onChange={(e) => handleStatusChange(ticket._id, e.target.value)}
                        >
                          <MenuItem value="Open">Open</MenuItem>
                          <MenuItem value="Close">Close</MenuItem>
                          <MenuItem value="In Progress">In Progress</MenuItem>
                          <MenuItem value="Resolved">Resolved</MenuItem>
                        </Select>
                      </TableCell>
                      <TableCell>
                        <Select
                          value={ticket.agentId || ''}
                          onChange={(e) => handleAgentChange(ticket._id, e.target.value)}
                          displayEmpty
                        >
                          <MenuItem value="" disabled>Select Agent</MenuItem>
                          {techSupportAgents.map(agent => (
                            <MenuItem key={agent._id} value={agent._id}>
                              {agent.username}
                            </MenuItem>
                          ))}
                        </Select>
                      </TableCell>
                    </TableRow>
                  ))}
              </TableBody>
            </Table>
          </TableContainer>

          <TablePagination
            rowsPerPageOptions={[10, 25, 50]}
            component="div"
            count={filteredTickets.length}
            rowsPerPage={rowsPerPage}
            page={page}
            onPageChange={handleChangePage}
            onRowsPerPageChange={handleChangeRowsPerPage}
          />
        </>
      )}
    </div>
  );
};

export default AdminTicketList;
