// export default Dashboard

import React from 'react';
import { useNavigate } from 'react-router-dom';
import { Button } from 'react-bootstrap';
import { Grid } from '@mui/material';
import { useAuth } from './AuthContext';
import { useEffect, useState } from 'react';
import axios from 'axios';
import './Dashboard.css';

const AdminDashboard = () => {
  const { user } = useAuth();

  const [userPermissions, setUserPermissions] = useState([]);
  const [isLoading, setLoading] = useState(true);

  const navigate = useNavigate();

  useEffect(() => {
    if (user) {
      const apiBaseUrl = process.env.REACT_APP_API_KEY;
      axios
        .get(`${apiBaseUrl}/auth/getAccess/${user}`)
        .then((response) => {
          setUserPermissions(response.data.permissions);
        })
        .catch((error) => {
          console.error('Error fetching user permissions:', error);
        })
        .finally(() => {
          setLoading(false);
        });
    }
  }, [user]);

  const hasPermission = (permission) => {
    return userPermissions.includes(permission);
  };

  const handleImageClick = (destination) => {
    navigate(destination);
  };

  const handleBackup = () => {
    const apiBaseUrl = process.env.REACT_APP_API_KEY;
    axios({
      method: 'GET',
      url: `${apiBaseUrl}/auth/backup`,
      responseType: 'blob',
    })
      .then((response) => {
        const url = window.URL.createObjectURL(new Blob([response.data]));
        const a = document.createElement('a');
        a.style.display = 'none';
        a.href = url;
        a.download = 'backup.zip';
        document.body.appendChild(a);
        a.click();
        window.URL.revokeObjectURL(url);
      })
      .catch((error) => {
        console.error('Backup failed:', error);
      });
  };

  return (
    <div className='container-fluid'>
      <Grid container spacing={2}>
        <Grid item xs={12}>
          <h1>WELCOME</h1>
        </Grid>
      </Grid>

      {isLoading ? (
        <img
          src={require('../../assets/images/loading.gif')}
          alt="Loading..."
          style={{ width: '100px', height: '100px' }}
        />
      ) : (
        <>
          <Grid container spacing={2} style={{ marginBottom: '20px' }}>
            {/* {hasPermission('createBill') && ( */}
              <Grid item xs={12}>
                <Button
                  onClick={() => navigate('/ticket-form')}
                  style={{
                    backgroundColor: '#8F00FF',
                    color: 'white',
                    border: 'none',
                    fontWeight: 'bold',
                    boxShadow: '0 2px 5px rgba(0, 0, 0, 0.2)', // Add the box-shadow property
                  }}
                >
                  CREATE TICKET
                </Button>
              </Grid>
            {/* )} */}
          </Grid>

            {/* {hasPermission('Bills') && ( */}
              <Grid item xs={6} sm={2}>
                <div
                  className="grid-item"
                  onClick={() => handleImageClick('/admin-tickets')}
                >
                  <img
                    src={require('../../assets/images/bill.jpg')}
                    alt="Bills"
                    className="image"
                  />
                  <span className="image-text">Tickets</span>
                </div>
              </Grid>
            {/* )} */}

            <Grid item xs={6} sm={2}>
                <div
                  className="grid-item"
                  onClick={() => handleImageClick('/regAgent')}
                >
                  <img
                    src={require('../../assets/images/bill.jpg')}
                    alt="Bills"
                    className="image"
                  />
                  <span className="image-text">Create Agent</span>
                </div>
              </Grid>

          {/* <Grid container spacing={2} style={{ marginBottom: '20px' }}>
            {hasPermission('Bills') && (
              <Grid item xs={6} sm={2}>
                <div
                  className="grid-item"
                  onClick={() => handleImageClick('/bil-list')}
                >
                  <img
                    src={require('../../assets/images/bill.jpg')}
                    alt="Bills"
                    className="image"
                  />
                  <span className="image-text">Bills</span>
                </div>
              </Grid>
            )}

            {hasPermission('LabReports') && (
              <Grid item xs={6} sm={2}>
                <div
                  className="grid-item"
                  onClick={() => handleImageClick('/reportList')}
                >
                  <img
                    src={require('../../assets/images/reports.jpg')}
                    alt="Lab Reports"
                    className="image"
                  />
                  <span className="image-text">Reports</span>
                </div>
              </Grid>
            )}

            {hasPermission('UpdatePatient') && (
              <Grid item xs={6} sm={2}>
                <div
                  className="grid-item"
                  onClick={() => handleImageClick('/patient-list')}
                >
                  <img
                    src={require('../../assets/images/patients.jpg')}
                    alt="Patients"
                    className="image"
                  />
                  <span className="image-text">Patients</span>
                </div>
              </Grid>
            )}

            {hasPermission('createDoctor') && (
              <Grid item xs={6} sm={2}>
                <div
                  className="grid-item"
                  onClick={() => handleImageClick('/doctor-list')}
                >
                  <img
                    src={require('../../assets/images/createDoctor.jpg')}
                    alt="Doctors"
                    className="image"
                  />
                  <span className="image-text">Doctors</span>
                </div>
              </Grid>
            )}

            {hasPermission('TestMaster') && (
              <Grid item xs={6} sm={2}>
                <div
                  className="grid-item"
                  onClick={() => handleImageClick('/test-master')}
                >
                  <img
                    src={require('../../assets/images/tests.jpg')}
                    alt="Test Master"
                    className="image"
                  />
                  <span className="image-text">Test Master</span>
                </div>
              </Grid>
            )}

            {hasPermission('TestMasterList') && (
              <Grid item xs={6} sm={2}>
                <div
                  className="grid-item"
                  onClick={() => handleImageClick('/test-list')}
                >
                  <img
                    src={require('../../assets/images/testlist.jpg')}
                    alt="Test List"
                    className="image"
                  />
                  <span className="image-text">Test List</span>
                </div>
              </Grid>
            )}
          </Grid>

          <Grid container spacing={2} style={{ marginTop: '20px' }}>
            {hasPermission('GroupMaster') && (
              <Grid item xs={6} sm={2}>
                <div
                  className="grid-item"
                  onClick={() => handleImageClick('/create-group-test')}
                >
                  <img
                    src={require('../../assets/images/grouptest.jpg')}
                    alt="Group Test Master"
                    className="image"
                  />
                  <span className="image-text">Create Group</span>
                </div>
              </Grid>
            )}

            {hasPermission('GroupMasterList') && (
              <Grid item xs={6} sm={2}>
                <div
                  className="grid-item"
                  onClick={() => handleImageClick('/group-list')}
                >
                  <img
                    src={require('../../assets/images/grouplist.jpg')}
                    alt="Group Test List"
                    className="image"
                  />
                  <span className="image-text">Group Test List</span>
                </div>
              </Grid>
            )} */}

            {/* {hasPermission('ProfileMaster') && (
              <Grid item xs={6} sm={2}>
                <div
                  className="grid-item"
                  onClick={() => handleImageClick('/create-profile')}
                >
                  <img
                    src={require('../../assets/images/tests.jpg')}
                    alt="Test Master"
                    className="image"
                  />
                  <span className="image-text">Profile Master</span>
                </div>
              </Grid>
            )}

            {hasPermission('ProfileMasterList') && (
              <Grid item xs={6} sm={2}>
                <div
                  className="grid-item"
                  onClick={() => handleImageClick('/profileList')}
                >
                  <img
                    src={require('../../assets/images/testlist.jpg')}
                    alt="Test List"
                    className="image"
                  />
                  <span className="image-text">Profile List</span>
                </div>
              </Grid>
            )} */}

            {/* {hasPermission('refMaster') && (
              <Grid item xs={6} sm={2}>
                <div
                  className="grid-item"
                  onClick={() => handleImageClick('/refMaster')}
                >
                  <img
                    src={require('../../assets/images/ref.jpg')}
                    alt="User Access"
                    className="image"
                  />
                  <span className="image-text">refMaster</span>
                </div>
              </Grid>
            )}



            {hasPermission('CollReports') && (
              <Grid item xs={6} sm={2}>
                <div
                  className="grid-item"
                  onClick={() => handleImageClick('/dailyCollection')}
                >
                  <img
                    src={require('../../assets/images/collreports.jpg')}
                    alt="Collection Reports"
                    className="image"
                  />
                  <span className="image-text">Accounts</span>
                </div>
              </Grid>
            )}

            {hasPermission('StaffManagement') && (
              <Grid item xs={6} sm={2}>
                <div
                  className="grid-item"
                  onClick={() => handleImageClick('/createUser')}
                >
                  <img
                    src={require('../../assets/images/staff.jpg')}
                    alt="User Management"
                    className="image"
                  />
                  <span className="image-text">Create User</span>
                </div>
              </Grid>
            )}

            {hasPermission('UserAccess') && (
              <Grid item xs={6} sm={2}>
                <div
                  className="grid-item"
                  onClick={() => handleImageClick('/userAccess')}
                >
                  <img
                    src={require('../../assets/images/userAccess.jpg')}
                    alt="User Access"
                    className="image"
                  />
                  <span className="image-text">User Access</span>
                </div>
              </Grid>
            )}


            {hasPermission('UserAccess') && (
              <Grid item xs={6} sm={2}>
                <div
                  className="grid-item"
                  onClick={() => handleImageClick('/orderList')}
                >
                  <img
                    src={require('../../assets/images/userAccess.jpg')}
                    alt="User Access"
                    className="image"
                  />
                  <span className="image-text">Order List</span>
                </div>
              </Grid>
            )}

 */}
          {/* </Grid> */}



          {/* <div>
            <button onClick={handleBackup}>Backup Database</button>
          </div> */}
        </>
      )}
    </div>
  );
};

export default AdminDashboard;
