// export default Header;

import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import { Navbar, Nav, NavDropdown } from 'react-bootstrap';
import { useAuth } from './AuthContext';
import { useNavigate } from 'react-router-dom';

const Header = () => {
    const navigate = useNavigate();
    const { user, logout, userName } = useAuth();
    const [userPermissions, setUserPermissions] = useState([]);
    const [isLoading, setLoading] = useState(true);

    useEffect(() => {
        if (user) {
            const apiBaseUrl = process.env.REACT_APP_API_KEY;
            fetch(`${apiBaseUrl}/auth/getAccess/${user}`)
                .then((response) => response.json())
                .then((data) => setUserPermissions(data.permissions))
                .catch((error) => console.error('Error fetching user permissions:', error))
                .finally(() => setLoading(false));
        }
    }, [user]);

    const hasPermission = (permission) => userPermissions.includes(permission);

    const handleLogout = () => {
        logout();
        navigate('/');
    };

    return (
        <div className='container-fluid'>
            <Navbar expand="lg" style={{ marginBottom: '20px', backgroundColor: '#8F00FF' }}>
                <Navbar.Brand as={Link} to="/dash">
                    <img
                        src={require('../../assets/images/logoFinals.png')}
                        alt="bb"
                        style={{ width: '70px', height: '40px' }}
                    />
                </Navbar.Brand>
                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                <Navbar.Collapse id="basic-navbar-nav" className="justify-content-start" style={{ color: 'white' }}>

                    
                    <Nav>
                        {user ? (
                            <>
                                <Navbar.Text className="text-white mr-3" style={{ marginLeft: '1100px' }}>Logged in as: {userName}</Navbar.Text>
                                <Nav.Link className="text-white" onClick={handleLogout}>
                                    Logout
                                </Nav.Link>
                            </>
                        ) : (
                            // Add login-related Nav.Link items for users who are not logged in
                            <>
                                {/* <Nav.Link as={Link} to="/login" style={{ color: 'white' }}>
                                    Login
                                </Nav.Link>
                                <Nav.Link as={Link} to="/register" style={{ color: 'white' }}>
                                    Register
                                </Nav.Link> */}
                            </>
                        )}
                    </Nav>
                </Navbar.Collapse>
            </Navbar>
        </div>
    );
};

export default Header;
