import React, { createContext, useContext, useState, useEffect } from 'react';

const AuthContext = createContext();

export const AuthProvider = ({ children }) => {
  const [user, setUser] = useState(null);
  const [userName, setUsername] = useState(null);
  const [token, setToken] = useState(localStorage.getItem('authToken'));

  useEffect(() => {
    // Check if a user is already authenticated (e.g., via token in localStorage)
    const storedToken = localStorage.getItem('authToken');
  
    if (storedToken) {
      // Retrieve user data from localStorage
      const userData = localStorage.getItem('userData');
      if (userData) {
        setUser(JSON.parse(userData));
      }
  
      // Retrieve userName from localStorage
      const userName = localStorage.getItem('userName');
      if (userName) {
        // Do something with the userName, e.g., set it in your component state.
        // Example:
        setUsername(userName);
      }
  
      setToken(storedToken);
    }
  }, []);
  
  const login = (userData, authToken, userName) => {
    setUser(userData);
    localStorage.setItem('userData', JSON.stringify(userData));
    localStorage.setItem('authToken', authToken);
    localStorage.setItem('userName', userName);
    setToken(authToken);
  };

  const logout = () => {
    setUser(null);
    localStorage.removeItem('userData');
    localStorage.removeItem('authToken');
    localStorage.removeItem('userName');
    setToken(null);
  };

  return (
    <AuthContext.Provider value={{ user, token, userName,login, logout }}>
      {children}
    </AuthContext.Provider>
  );
};

export const useAuth = () => {
  return useContext(AuthContext);
};
