import React, { useState } from 'react';
import axios from 'axios';
import { TextField, Button, Grid, Paper, Typography } from '@mui/material';

const AgentForm = () => {
  const [username, setName] = useState('');
  const [password, setNumber] = useState('');
  const [loading, setLoading] = useState(false);
  const [message, setMessage] = useState('');

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);
    setMessage('');

    try {
      const apiBaseUrl = process.env.REACT_APP_API_KEY;
      const response = await axios.post(`${apiBaseUrl}/auth/create-agent`, { username, password });
      setMessage(response.data.message);
      setName('');
      setNumber('');
    } catch (error) {
      console.error('Error creating agent:', error);
      setMessage('Failed to create agent');
    } finally {
      setLoading(false);
    }
  };

  return (
    <Paper style={{ padding: 16, marginTop: 16 }}>
      <Typography variant="h5" color="#8F00FF" gutterBottom>
        Create Agent
      </Typography>
      <form onSubmit={handleSubmit}>
        <Grid container spacing={2}>
          <Grid item xs={12}>
            <TextField
              label="Name"
              variant="outlined"
              fullWidth
              value={username}
              onChange={(e) => setName(e.target.value)}
              required
            />
          </Grid>
          <Grid item xs={12}>
            <TextField
              label="Password"
              variant="outlined"
              fullWidth
              value={password}
              onChange={(e) => setNumber(e.target.value)}
              required
            />
          </Grid>
          <Grid item xs={12}>
            <Button
              type="submit"
              variant="contained"
              color="primary"
              disabled={loading}
              style={{ backgroundColor: '#8F00FF', color: 'white' }}
            >
              {loading ? 'Creating...' : 'Create Agent'}
            </Button>
          </Grid>
          {message && (
            <Grid item xs={12}>
              <Typography color="textSecondary">{message}</Typography>
            </Grid>
          )}
        </Grid>
      </form>
    </Paper>
  );
};

export default AgentForm;
