import axios from 'axios';
import React, { useState } from 'react'
import { Grid, TextField, Button } from '@mui/material';
import { FormControl, InputLabel, MenuItem, Select } from '@mui/material';
import { TextareaAutosize } from '@mui/base/TextareaAutosize';
import { toast } from 'react-toastify';
import { useNavigate } from 'react-router-dom';

const TicketRegistration = () => {

    const navigate = useNavigate();
    const [doctorDetails, setdoctorDetails] = useState({
        // docId: '',
        name: '',
        email: '',
        password: '',
        num: '',
    });

    const handleInput = (e) => {
        const name = e.target.name;
        const value = e.target.value;

        setdoctorDetails({ ...doctorDetails, [name]: value });
    }

    const handleSubmit = (e) => {

        e.preventDefault(); // Prevent default form submission behavior

        // Here, you can collect all the data and submit it
        const submittedData = {
            doctorDetails
        };

        console.log('Submitted Data:', submittedData);

        // Assuming you have an API endpoint for submission
        const apiBaseUrl = process.env.REACT_APP_API_KEY;
        axios.post(`${apiBaseUrl}/auth/ticket-register`, submittedData)
            .then(response => {
                toast.success('Registered successfully'); // Display success message
                // console.log('Submission successful:', response.data);
                // Clear the data after successful submission
                setdoctorDetails({
                    // docId: '',
                    username: '',
                    email: '',
                    password: '',
                    num: '',
                });
                navigate('/');

            })
            .catch(error => {
                console.error('Submission error:', error);
            });

    }

    return (
        <>
            <div className='container'>
                <h5 style={{ textAlign: 'left', color: '#8F00FF' }}>Register</h5>
                <form action="" onSubmit={handleSubmit}>
                    <Grid container spacing={2}>
                        <Grid item xs={6} style={{ marginBottom: '16px' }}>
                            <div>
                                <FormControl fullWidth >
                                    <TextField variant="outlined" label="User Name" name="username" id="username" value={doctorDetails.username} onChange={handleInput} required />
                                </FormControl>
                            </div>
                        </Grid>

                        {/* <Grid item xs={6}>
                            <div>
                                <FormControl fullWidth >
                                    <TextField variant="outlined" label="Gender" name="drGender" id="drGender" value={doctorDetails.drGender} onChange={handleInput} />
                                </FormControl>
                            </div>
                        </Grid> */}
                    </Grid>

                    <Grid container spacing={2}>
                        <Grid item xs={6} style={{ marginBottom: '16px' }}>
                            <div>
                                <FormControl fullWidth >
                                    <TextField variant="outlined" label="Email" name="email" id="email" value={doctorDetails.email} onChange={handleInput} />
                                </FormControl>
                            </div>
                        </Grid>

                        <Grid item xs={6}>
                            <div>
                                <FormControl fullWidth >
                                    <TextField variant="outlined" label="Number" name="num" id="num" value={doctorDetails.num} onChange={handleInput} />
                                </FormControl>
                            </div>
                        </Grid>
                    </Grid>

                    <Grid container spacing={2}>
                        <Grid item xs={12} style={{ marginBottom: '16px' }}>
                            <div>
                                <FormControl fullWidth >

                                    <TextField variant="outlined" label="password" name="password" id="password" value={doctorDetails.password} onChange={handleInput} />
                                    {/* <TextareaAutosize variant="outlined" label="Address" name="drAddress" id="drAddress" minRows="10" value={doctorDetails.drAddress} onChange={handleInput}> </TextareaAutosize> */}
                                </FormControl>

                            </div>
                        </Grid>
                    </Grid>

                    <Grid container spacing={2}>
                        <Grid item xs={12}>
                            <div>
                                <Button type='submit' style={{ backgroundColor: '#8F00FF', color: 'white', border: 'none' }} >CREATE</Button>
                            </div>
                        </Grid>
                    </Grid>
                </form>
            </div>
        </>
    )
}

export default TicketRegistration