import React, { useState, useEffect } from 'react';
import axios from 'axios';
import {
  Table,
  TableContainer,
  TableHead,
  TableRow,
  TableCell,
  TableBody,
  TextField,
  Paper,
  IconButton,
  TablePagination,
  Grid,
  CircularProgress,
  MenuItem,
  Select,
} from '@mui/material';
import { useNavigate } from 'react-router-dom';
import { Button } from 'react-bootstrap';
import SearchIcon from '@mui/icons-material/Search';
import { toast } from 'react-toastify';
import { useAuth } from './AuthContext';

const TicketList = () => {
  const navigate = useNavigate();
  const [tickets, setTickets] = useState([]);
  const [searchTerm, setSearchTerm] = useState('');
  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(10);
  const [loading, setLoading] = useState(true);
  const { user } = useAuth();

  useEffect(() => {
    const fetchTickets = async () => {
      try {
        const apiBaseUrl = process.env.REACT_APP_API_KEY;
        const response = await axios.get(`${apiBaseUrl}/auth/tickets/${user}`);
        setTickets(response.data.tickets);
      } catch (error) {
        console.error('Error fetching tickets:', error);
      } finally {
        setLoading(false);
      }
    };

    if (user) {
      fetchTickets();
    }
  }, [user]);

  const searchTermLower = searchTerm.toLowerCase();

  const filteredTickets = tickets.filter((ticket) => 
    ticket.subject.toLowerCase().includes(searchTermLower) ||
    ticket.status.toLowerCase().includes(searchTermLower)
  );

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  const handleSubmit = () => {
    navigate('/ticket-form');
  };

  const handleRowClick = (ticketId) => {
    navigate(`/ticketDetail/${ticketId}`);
  };

  const handleStatusChange = async (ticketId, newStatus, e) => {
    try {
      const apiBaseUrl = process.env.REACT_APP_API_KEY;
      await axios.put(`${apiBaseUrl}/auth/update-ticket-status/${ticketId}`, { status: newStatus });
      // Update the local state to reflect the status change
      setTickets(tickets.map(ticket => ticket._id === ticketId ? { ...ticket, status: newStatus } : ticket));
      toast.success('Status Updated successfully'); // Display success message
    } catch (error) {
      console.error('Error updating ticket status:', error);
    }
  };

  return (
    <div className="container">
      <h5 style={{ textAlign: 'left', color: '#8F00FF' }}>Tickets</h5>
      <Grid container style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
        <Grid item>
          <Button
            onClick={handleSubmit}
            style={{
              backgroundColor: '#8F00FF',
              color: 'white',
              border: 'none',
              fontWeight: 'bold',
              boxShadow: '0 2px 5px rgba(0, 0, 0, 0.2)',
            }}
          >
            CREATE TICKET
          </Button>
        </Grid>
      </Grid>

      <TextField
        label="Search by Subject or Status"
        variant="outlined"
        fullWidth
        value={searchTerm}
        onChange={(e) => setSearchTerm(e.target.value)}
        InputProps={{
          endAdornment: (
            <IconButton>
              <SearchIcon />
            </IconButton>
          ),
        }}
        style={{ marginBottom: 16 }}
      />

      {loading ? (
        <CircularProgress />
      ) : (
        <>
          <TableContainer component={Paper}>
            <Table>
              <TableHead style={{backgroundColor:'#8F00FF'}}>
                <TableRow>
                  <TableCell style={{ color:'white' }}><strong>Ticket ID</strong></TableCell>
                  <TableCell style={{ color:'white' }}><strong>Subject</strong></TableCell>
                  <TableCell style={{ color:'white' }}><strong>Description</strong></TableCell>
                  <TableCell style={{ color:'white' }}><strong>Status</strong></TableCell>
                  <TableCell style={{ color:'white' }}><strong>Created Date</strong></TableCell>
                  <TableCell style={{ color:'white' }}><strong>Update Status</strong></TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {filteredTickets
                  .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                  .map((ticket) => (
                    <TableRow
                      key={ticket._id}
                      // onClick={() => handleRowClick(ticket._id)}
                      // style={{ cursor: 'pointer' }}
                    >
                      <TableCell>{ticket._id}</TableCell>
                      <TableCell>{ticket.subject}</TableCell>
                      <TableCell>{ticket.description}</TableCell>
                      <TableCell>{ticket.status}</TableCell>
                      <TableCell>{new Date(ticket.createdAt).toLocaleDateString()}</TableCell>
                      <TableCell>
                        <Select
                          value={ticket.status}
                          onChange={(e) => handleStatusChange(ticket._id, e.target.value, e)}
                        >
                          <MenuItem value="Open">Open</MenuItem>
                          <MenuItem value="Close">Close</MenuItem>
                          <MenuItem value="In Progress">In Progress</MenuItem>
                          <MenuItem value="Resolved">Resolved</MenuItem>
                        </Select>
                      </TableCell>
                    </TableRow>
                  ))}
              </TableBody>
            </Table>
          </TableContainer>

          <TablePagination
            rowsPerPageOptions={[10, 25, 50]}
            component="div"
            count={filteredTickets.length}
            rowsPerPage={rowsPerPage}
            page={page}
            onPageChange={handleChangePage}
            onRowsPerPageChange={handleChangeRowsPerPage}
          />
        </>
      )}
    </div>
  );
};

export default TicketList;
