// export default TicketForm;

import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import Typography from '@mui/material/Typography';
import Grid from '@mui/material/Grid';
import { toast } from 'react-toastify';
import { useAuth } from './AuthContext';
import axios from 'axios';

const TicketForm = () => {
  const navigate = useNavigate();
  const { user } = useAuth();

  const [ticketData, setTicketData] = useState({
    subject: '',
    description: '',
    attachment: null,
    creator: user,
  });

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setTicketData({
      ...ticketData,
      [name]: value,
    });
  };

  const handleFileChange = (e) => {
    setTicketData({
      ...ticketData,
      attachment: e.target.files[0], // Store the selected file
    });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    const formData = new FormData();
    formData.append('subject', ticketData.subject);
    formData.append('description', ticketData.description);
    formData.append('attachment', ticketData.attachment);
    formData.append('creator', ticketData.creator);

    try {
      const apiBaseUrl = process.env.REACT_APP_API_KEY;
      await axios.post(`${apiBaseUrl}/auth/create-ticket`, formData, {
        headers: {
          'Content-Type': 'multipart/form-data',
        },
      });
      toast.success('Ticket Created successfully'); // Display success message
      setTicketData({
        subject: '',
        description: '',
        attachment: null,
      });
      // Optionally, redirect the user to another page after successful submission
      navigate('/');
    } catch (error) {
      console.error('Error creating ticket:', error);
      toast.error('Error creating ticket');
    }
  };

  return (
    <div className="container">
      <Typography variant="h4" style={{ marginBottom: '30px' }}>Create New Ticket</Typography>
      <form onSubmit={handleSubmit}>
        <Grid container spacing={2}>
          <Grid item xs={12}>
            <TextField
              fullWidth
              id="subject"
              name="subject"
              label="Subject"
              value={ticketData.subject}
              onChange={handleInputChange}
              required
            />
          </Grid>
          <Grid item xs={12}>
            <TextField
              fullWidth
              id="description"
              name="description"
              label="Description"
              multiline
              rows={4}
              value={ticketData.description}
              onChange={handleInputChange}
              required
            />
          </Grid>
          <Grid item xs={12}>
            <input
              type="file"
              id="attachment"
              name="attachment"
              onChange={handleFileChange}
            />
          </Grid>
          <Grid item xs={12}>
            <Button type="submit" variant="contained" color="primary">
              Submit
            </Button>
          </Grid>
        </Grid>
      </form>
    </div>
  );
};

export default TicketForm;
