// import Registration from './Components/Reception/Registration';
import { BrowserRouter as Router, Route, Routes } from 'react-router-dom';
import { createRoot } from 'react-dom/client';
import Dashboard from './Components/Reception/Dashboard';
import Layout from './Components/Reception/Layout';
import { AuthProvider } from './Components/Reception/AuthContext';
import LoginPage from './Components/LoginPage';
import TicketRegistration from './Components/Reception/TicketRegistration';
import TicketForm from './Components/Reception/TicketForm';
import TicketList from './Components/Reception/TicketList';
import AdminLogin from './Components/AdminLogin';
import AdminTicketList from './Components/Reception/AdminTicketList';
import AgentForm from './Components/Reception/AgentForm';
import TechSupportLogin from './Components/TechSupportLogin';
import { Navigate } from 'react-router-dom';
import { useAuth } from './Components/Reception/AuthContext';


import 'bootstrap/dist/css/bootstrap.min.css';
import 'react-datepicker/dist/react-datepicker.css';
import AdminDashboard from './Components/Reception/AdminDashboard';
import AgentDashboard from './Components/Reception/AgentDashboard';
import AgentTicketList from './Components/Reception/AgentTicketList';

const PrivateRoute = ({ element }) => {
  const { user } = useAuth();

  if (!user) {
    return <Navigate to="/" />;
  }

  return element;
};

const App = () => {
  return (
    <AuthProvider>
      <Router>
        <Layout>
          <Routes>
            <Route path="/" element={<LoginPage />} />
            {/* <Route
              path="/"
              element={
                <PrivateRoute element={<TicketRegistration />} />
              }
            /> */}
            <Route path="/ticket-Register" element={<TicketRegistration />} />

            <Route path="/admin" element={<AdminLogin />} />

            <Route path="/admin-tickets" element={<AdminTicketList />} />

            <Route path="/adminDash" element={<AdminDashboard />} />

            <Route path="/regAgent" element={<AgentForm />} />

            <Route path="/agentDash" element={<AgentDashboard />} />

            <Route path="/agent" element={<TechSupportLogin />} />

            <Route path="/agentTicketList" element={<AgentTicketList />} />




            <Route
              path="/dash"
              element={
                <PrivateRoute element={<Dashboard />} />
              }
            />
            <Route
              path="/ticket-form"
              element={
                <PrivateRoute element={<TicketForm />} />
              }
            />
            <Route
              path="/ticket-list"
              element={
                <PrivateRoute element={<TicketList />} />
              }
            />
            
          </Routes>
        </Layout>
      </Router>
    </AuthProvider>
  );
};

const root = createRoot(document.getElementById('root'));
root.render(<App />);

export default App;
