import React, { useState } from 'react';
import axios from 'axios';
import { useAuth } from './Reception/AuthContext';
import { useNavigate, Link  } from 'react-router-dom';
import { Navigate } from 'react-router-dom';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import Grid from '@mui/material/Grid';


const TechSupportLogin = () => {
  const navigate = useNavigate();

  const [userData, setUserData] = useState({
    username: '',
    password: '',
  });

  const [error, setError] = useState(''); // Add error state

  const { login, user } = useAuth();

  if (user) {
    return <Navigate to="/agentDash" />;
  }

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setUserData({
      ...userData,
      [name]: value,
    });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    const { username, password } = userData;
    const credentials = { username, password };
    const apiBaseUrl = process.env.REACT_APP_API_KEY;

    axios
      .post(`${apiBaseUrl}/auth/agent-login`, credentials)
      .then((response) => {
        if (response.data.authenticated) {
          alert('Login successful!');
          const userId = response.data.userId;
          const token = response.data.token;
          const userName = response.data.user;
          login(userId, token, userName);
          navigate('/agentDash');
          
        } else {
          setError(response.data.message);
        console.error('Login failed:', response.data.message);
        }
      })
      .catch((error) => {
        console.error('Login error:', error);
      });
  };

  return (
    <div className="container">
      <Typography variant="h4" style={{marginBottom:'30px'}}>Tech Support Login</Typography>
      <form onSubmit={handleSubmit}>
        <Grid container spacing={2}>
          <Grid item xs={12}>
            <TextField
              fullWidth
              id="username"
              name="username"
              label="Username"
              value={userData.username}
              onChange={handleInputChange}
              required
              style={{ width: '300px' }}
            />
          </Grid>
          <Grid item xs={12}>
            <TextField
              fullWidth
              id="password"
              name="password"
              label="Password"
              type="password"
              value={userData.password}
              onChange={handleInputChange}
              required
              style={{ width: '300px' }}
            />
          </Grid>
          <Grid item xs={12}>
            <Button type="submit" variant="contained" color="primary" style={{ backgroundColor: '#8F00FF', color: 'white', border: 'none' }}>
              Login
            </Button>
          </Grid>
          {/* <Grid item xs={12}>
            <Typography variant="body2" color="textSecondary">
              Not registered? <Link to="/ticket-register">Register here</Link>
            </Typography>
          </Grid> */}
          {error && (
            <Grid item xs={12}>
              <Typography variant="body2" color="error">
                {error}
              </Typography>
            </Grid>
          )}
        </Grid>
      </form>
    </div>
  );
};

export default TechSupportLogin;
